const app = {
  state: {
    activeMenuItem: {},
  },
  mutations: {
    setActiveMenuItem(state, log) {
      state.activeMenuItem = {};
      setTimeout(() => {
        state.activeMenuItem = Object.assign({}, log);
      }, 200);
    },
  },
  actions: {
    setActiveMenuItem({ commit }, log) {
      commit("setActiveMenuItem", log);
    },
  },
  getters: {
    getActiveMenuItem(state) {
      return state.activeMenuItem;
    },
  },
};

export default app;
