<template>
  <a-layout id="ly-container">
    <a-layout-sider
      v-model="collapsed"
      :trigger="null"
      collapsible
    >
      <div class="logo" />
      <menu-list
        @listenTags="listenTags"
        :ask="ask"
      ></menu-list>
    </a-layout-sider>
    <a-layout>
      <a-layout-header>
        <a-icon
          class="trigger"
          :type="collapsed ? 'menu-unfold' : 'menu-fold'"
          @click="() => (collapsed = !collapsed)"
        />
        <div class="ly-head_title">
          <router-link :to="{ path: '/' }">
            <a>首页 |</a>
          </router-link>
          <transition name="show">
            <a v-if="getActiveMenuItem.name">
              {{ getActiveMenuItem.name || "" }}</a>
          </transition>
        </div>
      </a-layout-header>
      <div class="ly-tag">
        <a-tag
          v-for="item in tags"
          :key="item.key"
          :color="getActiveMenuItem.key == item.key ? '#1a90fe': ''"
          :closable="item.closable"
          @mouseenter="enterTag(item)"
          @mouseleave="leaveTag(item)"
          @close="removeTag(item)"
          @click="changeActiveItem(item)"
        >
          {{item.name}}
        </a-tag>
      </div>
      <a-layout-content :style="{
          margin: '15px 16px',
          padding: '10px',
          background: '#fff',
          minHeight: '280px',
        }">
        <router-view></router-view>
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>
<script>
import Menu from "./menuList";
export default {
  components: {
    [Menu.name]: Menu,
  },
  data() {
    return {
      collapsed: false,
      tags: [],
      ask: [],
    };
  },
  mounted() {},
  methods: {
    changeActiveItem(row) {
      this.ask = [row.key];
      row.closable = false;
      this.setActiveMenuItem(row);
      this.$router.push(row.path);
    },
    enterTag(row) {
      if (row.key == this.getActiveMenuItem.key) {
        row.closable = false;
      } else {
        row.closable = true;
      }
    },
    leaveTag(row) {
      row.closable = false;
    },
    listenTags(arr) {
      arr.forEach((ele) => {
        ele.closable = false;
      });
      this.tags = arr;
    },
    // 从sessionStorage中删除某个tag
    removeTag(tag) {
      const tags = JSON.parse(window.sessionStorage.getItem("tags"));
      let arr = [...tags];
      arr.forEach((ele) => {
        ele.closable = false;
      });
      let n = arr.findIndex((ele) => {
        return ele.key == tag.key;
      });
      arr.splice(n, 1);
      this.tags = arr;
      window.sessionStorage.setItem("tags", JSON.stringify(arr));
    },
  },
};
</script>
